header{
    padding-left: 1.25rem ;
    padding-right: 1.25rem;
    width: 100vw;
    position: fixed;
    z-index: 10; 
    mix-blend-mode: difference;
}

nav{
    display: flex;
    /* justify-content: space-between; */
}

.topnav{
    padding-top: -12%;
}

a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-family: "FKGroteskNeueTrial-Bold", "Helvetica Neue", sans-serif;
    font-size: 1.5rem;
    font-weight: 55;
}

.name{
    width: 100%;
}

.name a{
    font-size:7.7vw;
    font-family: "PL", "Helvetica Neue", sans-serif !important;
    line-height: 6vw;
 }

 .name img{
     
    mix-blend-mode: difference;

 }

.left a{
    font-family: "FKGroteskNeueTrial-Black", "Helvetica Neue", sans-serif;
    
}

.left{
    width: 100%;
    text-align: left;
}

.left,.right a{
     
}

.right{
    width: 100%;
    text-align: right;
}

.center{
    width: 20%;
    text-align: center;
}