.proj{
    width: 100%;
    display: block;
}


.intro{
    width: 100%;
    text-align: center;
}

.project-info{
    margin-top: 5%;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.iz{
    justify-content: flex-start;
    display: flex;
    text-align: left;
}

.iz p{
    margin-right: 1rem;
}

.der p{
    margin-left: 1rem;
}

.der{
    justify-content: flex-end;
    text-align: right;
    display: flex;
}

.nombre{
    text-transform:capitalize;
    font-family: "EditorialNewUltralightItalic", "Helvetica Neue", sans-serif;
    font-size:8rem;
    letter-spacing: -0.4rem;
}

.desc{
    text-transform:capitalize;
    font-family: "EditorialNewUltralight", "Helvetica Neue", sans-serif;
    font-size: 1.5rem;
}

.subtag{
    text-transform: uppercase;
    font-family: "AberMono-Bold", "Helvetica Neue", sans-serif;
    font-size: 1.25rem;
    font-weight: 55;
}

.cover{
    margin-top: 5%;
}

.copy-project{
    line-height: 115%;
    font-family: "FKGroteskNeueTrial-Regular", "Helvetica Neue", sans-serif;
    font-size: 3.9rem;
}




